import { Action } from '@ngrx/store';

export const PERIODS_LOAD = '[NCP Periods] load';
export const PERIODS_LOAD_SUCCESS = '[NCP Periods] load success';
export const PERIODS_LOAD_FAILURE = '[NCP Periods] load failure';

export class PeriodsLoadAction implements Action {
	public readonly type = PERIODS_LOAD;
}

export class PeriodsLoadSuccessAction implements Action {
	public readonly type = PERIODS_LOAD_SUCCESS;
	constructor(public payload: { start_time: string, end_time: string, type: string }[]) {}
}

export class PeriodsLoadFailureAction implements Action {
	public readonly type = PERIODS_LOAD_FAILURE;
}

export type PeriodsActions
	= PeriodsLoadAction
	| PeriodsLoadSuccessAction
	| PeriodsLoadFailureAction;
