import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';

import { ApiService } from '@app/core/services/api';
import { DataStore } from '@app/core/services/data';

import { tap, switchMap, catchError, map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { tag } from 'rxjs-spy/operators';
import { NCPService } from '@app/ncp-shared/services/ncp.service';
import { Winners } from '@app/ncp-shared/models/ncp.model';
import { environment } from '@app-env/environment';
import { DateSelectOption } from '@app/shared/models/common.model';
import { FormControl, Validators } from '@angular/forms';
import dayjs from 'dayjs';


@Component({
	selector: 'ncp-winners',
	templateUrl: 'winners.component.html',
	styleUrls: ['winners.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class WinnersComponent implements OnInit {
	public dateSelect: FormControl;

	public weeks$: Observable<DateSelectOption[]> = of([]);
	public winners$: Observable<Winners>;

	constructor(
		private _api: ApiService,
		private _ncp: NCPService,
		private _data: DataStore,
	) {
		this.dateSelect = new FormControl(null, [
			Validators.required,
			Validators.pattern('[0-9]{4}-[0-9]{2}-[0-9]{2}'),
		]);
	}

	ngOnInit() {
		let activePeriod: string;
		this.weeks$ = this._ncp.getPeriod('week').pipe(
			map(periods => periods
				.map((period, i) => {
					const periodStart = dayjs(period.from);
					const periodEnd = dayjs(period.until);

					if (dayjs().isAfter(periodStart)) activePeriod = periodStart.format('YYYY-MM-DD');

					return {
						value: periodStart.format('YYYY-MM-DD'),
						text: `${i + 1}. hét: ${periodStart.format('MM. DD.')} - ${periodEnd.format('MM. DD.')}`
					};
				})
			),
			tap(_ => {
				console.log('active period start = ', activePeriod);
				if (activePeriod) this.dateSelect.setValue(activePeriod);
			})
		);

		this.winners$ = this.dateSelect.valueChanges.pipe(
			switchMap(
				date => this._data.get<Winners>(
					'winners_' + date,
					() => this._api.request(
						{
							url: environment.NCP_API_URL + 'ncp/winners',
							method: 'GET',
							data: {
								date,
								period_type: 'week'
							}
						}
					).pipe(
						catchError(err => { console.log('API error caught', err); return of(null); }),
					)
				)
			),
			// tag('winners'),
		);

	}
}
