import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { PlatformLocation } from '@angular/common';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '@app/core/services/api';
import { ErrorService } from '@app/core/services/error';
import { RegistrationEndpoints, Registration } from '@app/ncp-shared/models/ncp.model';
import { environment } from '@app-env/environment';


@Component({
	selector:		'ncp-modal-registration',
	templateUrl:	'registration.component.html',
	styleUrls:		['registration.component.scss'],
})
export class RegistrationModalComponent implements OnInit {
	@Input() public email: string;

	public registrationForm: FormGroup;
	public submitDisabled: boolean;
	public errorMessages: string[] = [];

	constructor(
		private apiProvider: ApiService,
		private _location: PlatformLocation,
		public activeModal: NgbActiveModal,
		public errorProvider: ErrorService,
	) {
		this._location.onPopState(() => this.activeModal.dismiss());
	}



	public ngOnInit() {
		this.registrationForm = new FormGroup({
			email: new FormControl('', [
				Validators.required,
				Validators.email,
			]),
			last_name: new FormControl('', [
				Validators.required,
			]),
			first_name: new FormControl('', [
				Validators.required,
			]),
			accept_terms: new FormControl('', [
				Validators.requiredTrue,
			]),
		});

		this.registrationForm.reset({
			email: this.email,
		});

		this.submitDisabled = false;
	}

	public submitRegistration() {
		this.submitDisabled = true;
		this.errorMessages = [];

		const formValues = this.registrationForm.value;

		const node: RegistrationEndpoints = 'register';
		const data: Registration = {
			// email: formValues.email !== '' ? sha256(formValues.email.toLowerCase()) : '',
			email: formValues.email,
			first_name: formValues.first_name,
			last_name: formValues.last_name,
			accept_terms: formValues.accept_terms,
		};

		this.apiProvider.request({
			url: environment.NCP_API_URL + 'ncp/user/register',
			method: 'POST',
			data,
		}).subscribe(
			(result) => {
				console.log('registration request succeeded.', result);
				this.activeModal.close(result);
			},
			(errors) => {
				console.log('registration request rejected.', errors);
				// this.handleError(error);
				this.errorMessages = errors.map(error => this.errorProvider.get(error));
				console.log('errorMessages', this.errorMessages);
				this.submitDisabled = false;
			}
		);
	}
}
