import { Injectable } from '@angular/core';
import { Observable, of, defer } from 'rxjs';
import { switchMap, catchError, map } from 'rxjs/operators';

import { Action } from '@ngrx/store';
import { Actions, Effect, ofType } from '@ngrx/effects';

// import { SiteAPIService } from '../../services/site.service';
import * as SiteActions from '../actions/periods.actions';
import * as Sentry from '@sentry/browser';
import { ApiService } from '@app/core/services/api';
import { environment } from '@app-env/environment';


@Injectable()
export class PeriodsEffects {

	constructor(
		private actions$: Actions,
		private _api: ApiService,
	) {}

	@Effect()
	loadPeriods$: Observable<Action> = this.actions$.pipe(
		ofType(SiteActions.PERIODS_LOAD),
		switchMap(() => {
			return this._api.request({
				url: environment.NCP_API_URL + 'ncp/periods',
				method: 'GET',
			}).pipe(
				map(periods => new SiteActions.PeriodsLoadSuccessAction(periods)),
				catchError(error => {
					Sentry.withScope(scope => {
						scope.setTag('module', 'site');
						scope.setExtra('error', error);
						Sentry.captureMessage('NCP API error');
					});
					return of(new SiteActions.PeriodsLoadFailureAction());
				})
			);
		})
	);

	// @Effect()
	// init$: Observable<Action> = defer(() => {
	// 	console.log('PeriodsEffects: init$');
	// 	return of(new SiteActions.PeriodsLoadAction());
	// });
}
