import { Periods, Period } from '../../models/ncp.model';
import * as fromSite from '../actions/periods.actions';


export type State = Periods | 'loading';

const initialState: State = {};


export function reducer(state = initialState, action: fromSite.PeriodsActions): State {
	if (!action) {
		return state;
	}

	switch (action.type) {

		case fromSite.PERIODS_LOAD: {
			return 'loading';
		}

		case fromSite.PERIODS_LOAD_SUCCESS: {
			const periods: Periods = action.payload.reduce((a, c) => {
				const period: Period = { from: c.start_time, until: c.end_time };
				if (c.type === 'promotion') {
					a[c.type] = period;
				} else {
					if (typeof a[c.type] === 'undefined') a[c.type] = [];
					a[c.type].push(period);
				}
				return a;
			}, {});

			return periods;
		}

		case fromSite.PERIODS_LOAD_FAILURE: {
			return {};
		}

		default:
			return state;
	}
}
