import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';
import * as fromNCP from '../store';
import { Injectable } from '@angular/core';
import { first, tap, filter, pluck } from 'rxjs/operators';
import { tag } from 'rxjs-spy/operators/tag';
import { PeriodTypes, Period } from '../models/ncp.model';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from '@app/core/services/api';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MessageModalComponent, MessageModalParams } from '@app/shared/modals';
import { ShippingAddressModalComponent  } from '@app/ncp-shared/modals';
import { environment } from '@app-env/environment';

// @Injectable({
// 	providedIn: NCPModule
// })
@Injectable()
export class NCPService {

	constructor(
		private _store: Store<fromNCP.State>,
		private _route: ActivatedRoute,
		private _api: ApiService,
		private _router: Router,
		private _modalService: NgbModal,
	) {

		this._route.queryParams.pipe(
			filter(params => params.token),
			pluck('token'),
			tap(token => console.log('shippingAddressConfirmation token:', token)),
		).subscribe(token => {
			const newURL = this._router.url.substring(0, this._router.url.indexOf('?'));
			this._api.request({
				url: environment.NCP_API_URL + 'ncp/winner',
				method: 'GET',
				data: {
					token
				},
			}).subscribe(
				(data) => {
					const modal = this._modalService.open(ShippingAddressModalComponent, { windowClass: 'modal-shipping' });
					modal.componentInstance.userData = {
						token: data.token,
						email: data.upload.user.email,
						name: `${data.upload.user.last_name} ${data.upload.user.first_name}`,
					};
					modal.result.then(
						(result) => {
							console.log('showShippingModal() result', result);
							this.showMessageModal({title: 'Sikeres adatbeküldés', message: 'Köszönjük az adatok megadását! Kollégánk keresni fog!'});
							this._router.navigateByUrl(newURL);
						},
						() => {
							this._router.navigateByUrl(newURL);
						}
					);
				},
				(errors) => {
					errors = errors.map(error => typeof error === 'object' ? error.code : error);
					console.error(errors);
					if (errors.includes('token:already_confirmed')) {
						this.showMessageModal({ title: 'Hiba', message: 'Ezt a megerősító kódot már használták!' });
					} else {
						this.showMessageModal({ title: 'Hiba', message: 'Nem megfelelő megerősítő kód!' });
					}
					this._router.navigateByUrl(newURL);
				},
			);
		});
	}

	public showMessageModal({ title, message }: MessageModalParams): Promise<any> {
		const modal = this._modalService.open(MessageModalComponent);
		modal.componentInstance.title = title;
		modal.componentInstance.message = message;
		return modal.result;
	}


	// PERIODS

	private _loadPeriods(): void {
		this._store.select(fromNCP.isPeriodsLoadingOrLoaded).pipe(
			first()
		).subscribe(
			isPeriodsLoadingOrLoaded => {
				if (!isPeriodsLoadingOrLoaded) {
					this._store.dispatch(new fromNCP.PeriodsLoadAction());
				}
			},
		);
	}

	public getPeriod(type: 'promotion'): Observable<Period>;
	public getPeriod(type: PeriodTypes): Observable<Period[]>;
	public getPeriod(type: string): Observable<Period[]>;
	public getPeriod(type: 'promotion' | PeriodTypes | string): Observable<Period | Period[]> {
		this._loadPeriods();
		return this._store.select(fromNCP.getPeriod(type)).pipe(
			filter(period => !!period),
			// tap(period => console.log(`NCPService.getPeriod('${type}') = `, period))
		);
	}

	public getPromotionPeriod(): Observable<Period> {
		this._loadPeriods();
		return this._store.select(fromNCP.getPromotionPeriod).pipe(
			filter(period => !!period),
			// tap(period => console.log(`NCPService.getPromotionPeriod() = `, period))
		);
	}

}
