import { createSelector, MemoizedSelector } from '@ngrx/store';

import { PeriodTypes, Period } from '../../models/ncp.model';

import * as fromNCP from '../reducers';

export const isPeriodsLoading = createSelector(
	fromNCP.getPeriodsState,
	(periods: fromNCP.PeriodsState) => periods === 'loading'
);

export const isPeriodsLoaded = createSelector(
	fromNCP.getPeriodsState,
	(periods: fromNCP.PeriodsState) => periods.hasOwnProperty('promotion')
);

export const isPeriodsLoadingOrLoaded = createSelector(
	fromNCP.getPeriodsState,
	(periods: fromNCP.PeriodsState) => periods === 'loading' || periods.hasOwnProperty('promotion')
);

export function getPeriod(period: 'promotion'): MemoizedSelector<object, Period>;
export function getPeriod(period: PeriodTypes): MemoizedSelector<object, Period[]>;
export function getPeriod(period: string): MemoizedSelector<object, Period[]>;
export function getPeriod(period: 'promotion' | PeriodTypes | string): MemoizedSelector<object, Period | Period[]> {
	return createSelector(
		fromNCP.getPeriodsState,
		(periods: fromNCP.PeriodsState) => {
			if (periods === 'loading') return undefined;
			return period === 'promotion' ? periods.promotion : periods[period];
		}
	);
}

export const getPromotionPeriod = createSelector(
	getPeriod('promotion'),
	(periods: Period) => periods
);
