import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { PlatformLocation } from '@angular/common';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '@app/core/services/api';
import { ErrorService } from '@app/core/services/error';
import { ShippingAddressEndpoints, ShippingAddress } from '@app/ncp-shared/models/ncp.model';
import { environment } from '@app-env/environment';


@Component({
	selector:		'ncp-modal-shipping-address',
	templateUrl:	'shipping_address.component.html',
})
export class ShippingAddressModalComponent implements OnInit {
	@Input() public userData: { email: string, name: string, token: string };

	public shippingAddressForm: FormGroup;
	public submitDisabled: boolean;
	public errorMessages: string[] = [];

	constructor(
		private _api: ApiService,
		private _location: PlatformLocation,
		public activeModal: NgbActiveModal,
		public errorProvider: ErrorService,
	) {
		this._location.onPopState(() => this.activeModal.dismiss());
	}

	public ngOnInit() {
		this.shippingAddressForm = new FormGroup({
			token: new FormControl('', [
				Validators.required,
			]),
			email: new FormControl('', [
				Validators.required,
				Validators.email,
			]),
			shipping_name: new FormControl('', [
				Validators.required,
			]),
			shipping_postal_code: new FormControl('', [
				Validators.required,
				Validators.pattern(/^[1-9]\d{3}$/),
			]),
			shipping_city: new FormControl('', [
				Validators.required,
			]),
			shipping_street_address: new FormControl('', [
				Validators.required,
			]),
			shipping_phone_area: new FormControl('', [
				Validators.required,
				Validators.pattern(/^(1|\d{2})$/),
			]),
			shipping_phone_subscriber: new FormControl('', [
				Validators.required,
				Validators.pattern(/^\d{6,7}$/),
			]),
			shipping_phone_country: new FormControl('', [
				Validators.required,
			]),
		});

		console.log(this.userData);
		this.shippingAddressForm.reset({
			email: this.userData.email,
			token: this.userData.token,
			shipping_name: this.userData.name,
			shipping_phone_country: '+36',
		});

		this.submitDisabled = false;
	}

	public submitShippingAddress() {
		this.submitDisabled = true;
		this.errorMessages = [];

		const data: ShippingAddress = this.shippingAddressForm.value;

		this._api.request({
			url: environment.NCP_API_URL + 'ncp/winner/confirm',
			method: 'POST',
			data,
		}).subscribe(
			(result) => {
				console.log('setShippingAddress request succeeded.', result);
				this.activeModal.close(result);
			},
			(errors) => {
				console.log('setShippingAddress request rejected.', errors);
				this.errorMessages = errors.map(error => this.errorProvider.get(error));
			},
			() => {
				console.log('setShippingAddress request completed.');
				this.submitDisabled = false;
			}
		);
	}
}
