import { ActionReducerMap, createFeatureSelector, createSelector } from '@ngrx/store';
import * as fromPeriods from './periods.reducers';


export { State as PeriodsState } from './periods.reducers';


export interface State {
	periods: fromPeriods.State;
}

export const reducers: ActionReducerMap<State> = {
	periods: fromPeriods.reducer,
};

const getState = createFeatureSelector<State>('ncp');

export const getPeriodsState = createSelector(
	getState,
	(state: State) => state.periods
);
