import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable, of } from 'rxjs';
import { map, first } from 'rxjs/operators';

import { environment } from '@app-env/environment';
import { ApiService } from '@app/core/services/api';
import { ErrorService } from '@app/core/services/error';

import { NCPService } from '@app/ncp-shared/services/ncp.service';
import { Upload, CodeTypes, UploadEndpoint, UploadCommon } from '@app/ncp-shared/models/ncp.model';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MessageModalComponent, MessageModalParams } from '@app/shared/modals/message/message.component';
import { RegistrationModalComponent } from '@app/ncp-shared/modals';

import { RecaptchaComponent } from 'ng-recaptcha';

import dayjs from 'dayjs';
import { SharedStore } from '@app/core/services/shared';
import { Router } from '@angular/router';

type DateSelectOption = {
	value: string,
	text: string,
};

@Component({
	selector: 'ncp-upload',
	templateUrl: 'upload.component.html',
	styleUrls: ['upload.component.scss'],
})
export class UploadComponent implements OnInit {

	static REGEX_EMAIL = /^[\w.%+-]+@[\w.-]+\.[a-z]{2,}$/i;
	static REGEX_CODE_GENERATED = /^[0-9A-Z]{2}$/i;
	static REGEX_CODE_AP = /^[0-9A-SU-Z]\d{8}$/i;
	static REGEX_CODE_EXTRA = /^11\d{14}(?:\d{5})?$/;
	static REGEX_CODE_SPECIAL = /^(?:TESTCODE)$/i;
	static REGEX_RECEIPT_NUMBER = /^\d{4}\/\d{5}$/;

	public model: Upload;
	public errorMessages: string[] = [];

	public uploadForms: { [key in CodeTypes]?: FormGroup } = {};

	public selectedCodeType: CodeTypes = 'ap';

	public purchaseDates$: Observable<DateSelectOption[]> = of([]);
	public showReceiptNumber: boolean = false;
	public submitDisabled: boolean = false;
	public isSpecialCode: boolean = false;

	public recaptchaSiteKey: string;
	public captchaResponse: string = '';

	@ViewChild(RecaptchaComponent, { static: false }) private captchaRef: RecaptchaComponent;

	constructor(
		public router: Router,
		public errorProvider: ErrorService,
		private _modal: NgbModal,
		private _api: ApiService,
		private _ncp: NCPService,
		private _shared: SharedStore,
	) {
	}

	public ngOnInit() {

		// COMMON

		const today = dayjs();

		// AP CODE

		this.purchaseDates$ = this._ncp.getPromotionPeriod().pipe(
			map(period => {
				const purchaseDates = [];
				let currDate = dayjs(period.from);
				const lastDate = dayjs(Math.min(dayjs(period.until).valueOf(), today.valueOf()));
				while (currDate.isBefore(lastDate)) {
					purchaseDates.push({
						value: currDate.format('YYYY-MM-DD'),
						text: currDate.format('MM. DD.'),
					});
					currDate = currDate.add(1, 'day');
				}
				return purchaseDates;
			})
		);

		this.uploadForms['ap'] = new FormGroup({
			email: new FormControl('', [
				Validators.required,
				Validators.pattern(UploadComponent.REGEX_EMAIL),
			]),
			code: new FormControl('', [
				Validators.required,
				Validators.pattern(UploadComponent.REGEX_CODE_AP),
				Validators.maxLength(9),
			]),
			purchase_time: new FormGroup({
				date: new FormControl(today.format('YYYY-MM-DD'), [
					Validators.required,
				]),
				hour: new FormControl('', [
					Validators.required,
					Validators.pattern(/^([01]?[0-9]|2[0-3])$/),
					Validators.maxLength(2),
				]),
				minute: new FormControl('', [
					Validators.required,
					Validators.pattern(/^(0?[0-9]|[1-5][0-9])$/),
					Validators.maxLength(2),
				]),

			})
		});

		// COMMON

		this._ncp.getPromotionPeriod().pipe(
			first()
		).subscribe(period => {
			const isPromotionStarted = today.isAfter(dayjs(period.from));
			const isPromotionEnded = today.isAfter(dayjs(period.until));

			if (isPromotionEnded || !isPromotionStarted) {
				this.uploadForms['ap'].disable();
				this.submitDisabled = true;
				setTimeout(() => {
					// this is to avoid 'ExpressionChangedAfterItHasBeenCheckedError'. see also https://github.com/ng-bootstrap/ng-bootstrap/issues/1252
					if (!isPromotionStarted) {
						this.showMessageModal({ title: 'Úgy tűnik, egy kicsit korán érkeztél.', message: `<p>Nyereményjátékunk csak később indul! Ha vásároltál már promóciós csomagolású 1 literes SIÓ terméket és szeretnél részt venni a nyereményjátékon, őrizd meg a blokkot! A kódot 2020. március 16. és 2020. május 10. között tudod feltölteni itt az oldalon.</p><p><a href="/assets/pdf/jatekszabalyzat-szamhaboru.pdf" target="_blank" class="btn btn-green">Játékszabályzat</a></p>`, type: 'promoBefore', options: { backdrop: 'static', keyboard: false } });
					} else {
						this.showMessageModal({ title: 'Úgy tűnik, egy kicsit elkéstél.', message: `<p>Nyereményjátékunk már lezárult!</p>` });
					}
				});
			}
		});

		if (environment.SERVICE_RECAPTCHA_KEY) {
			this.recaptchaSiteKey = environment.SERVICE_RECAPTCHA_KEY;
		}
	}


	private _getActiveForm() {
		return this.uploadForms[this.selectedCodeType];
	}

	public submitUpload() {
		console.log('submitUpload() called.');

		this.submitDisabled = true;

		if (!this.captchaResponse) {
			console.log('executing captcha');
			this.captchaRef.execute();
			return;
		}

		let node: UploadEndpoint;
		let data: Upload;

		const formValues = this._getActiveForm().value;
		const commonData: UploadCommon = {
			email: formValues.email, // sha256: formValues.email !== '' ? sha256(formValues.email.toLowerCase()) : ''
			code: formValues.code,
			'g-recaptcha-response': this.captchaResponse,
		};
		node = 'upload-ap';
		data = {
			_type: 'ap',
			...commonData,
			purchase_time:
				(formValues.purchase_time.date !== '' && formValues.purchase_time.hour !== '' && formValues.purchase_time.minute !== '')
				?
				`${formValues.purchase_time.date} ${formValues.purchase_time.hour}:${formValues.purchase_time.minute}:00`
				:
				''
			,
		};
		if (formValues.receipt_number) {
			data.receipt_number = formValues.receipt_number;
		}

		this._api.request({
			url: environment.NCP_API_URL + 'ncp/code/upload-ap',
			method: 'POST',
			data,
		}).subscribe(
			(result) => this.handleUploadSuccess(result, data),
			(error) => this.handleUploadError(error, data),
		);
	}

	public async handleUploadError(errors: string[], data: Upload) {

		this.errorMessages = [];

		console.log(errors);

		if (errors.includes('email:not_found')) {
			try {
				await this.showRegisterModal();
				this.submitUpload();
			} catch (e) {
				// ignore dismiss exception
			}
		} else if (errors.includes('code:already_uploaded')) {
			this.uploadForms['ap'].addControl('receipt_number', new FormControl('', [
				Validators.required,
				Validators.pattern(UploadComponent.REGEX_RECEIPT_NUMBER),
				Validators.maxLength(10),
			]));
			this.showReceiptNumber = true;

			this.errorMessages = [this.errorProvider.get('code:already_uploaded')];

			// this.showMessageModal({ title: 'Hiba', message: this.errorMessages.join('<br>') });
		} else if (errors.includes('recaptcha:invalid')) {
			// this.captchaRef.execute();
			return;
		} else {
			this.errorMessages = errors.map(error => this.errorProvider.get(error));
			console.log('errorMessages', this.errorMessages);
			/// this.showMessageModal({ title: 'Hiba', message: this.errorMessages.join('<br>') });
		}

		this.submitDisabled = false;

		this.captchaRef.reset();
	}

	public handleUploadSuccess(result: any, data: Upload) {
		const email = this._getActiveForm().value.email;
		const formValues = this.uploadForms['ap'].value;

		this.uploadForms['ap'].reset({
			email,
			purchase_time: {
				date: formValues.purchase_time.date,
			}
		});
		this.uploadForms['ap'].removeControl('receipt_number');
		this.showReceiptNumber = false;

		this.errorMessages = [];
		this.submitDisabled = false;
		let modalPromise = null;

		if (result.prizes) {
			if (result.prizes.length > 0) {
				modalPromise = this.showMessageModal({
					title: 'Gratulálunk, nyertél!',
					message:
						`<p><strong>Tiéd a ${result.prizes[0].name}!</strong><br>` +
						`A további teendőket olvasd a megadott e-mail címedre elküldött levelünkben, a játék lezártáig pedig őrizd meg a blokkod, hogy a későbbiekben igazolni tudd a nyertes kódod!<br>Ha még játszanál, tölts fel új AP kódot, hogy a többi nyereményért is indulhass!</p>`,
					buttons: [
						{
							caption: 'Kattints ide, ha az extra játékunkkal is szeretnél nyerni!',
							type: 'dismiss',
							class: 'btn btn-green'
						}
					],
					trackingId: `track_winning_${data._type}`,
				});
			} else {
				modalPromise = this.showMessageModal({
					title: 'Sajnos most nem nyertél!',
					message: `<p>Játssz újra egy másik kóddal! De ne búsulj, még versenybe szállhatsz az extra nyereményekért.</p>`,
					buttons: [
						{
							caption: 'Irány az extra játék!',
							type: 'dismiss',
							class: 'btn btn-green'
						}
					],
					trackingId: `track_not_winning_${data._type}`,
				});
			}
		} else {
			modalPromise = this.showMessageModal({
				title: 'Sikeres kódfeltöltés!',
				message: `<p>Szeretnél játszani további nyereményekért? Fejleszd magadat számháborúban, és légy te a bajnok a szabadban is!</p>`,
				buttons: [
					{
						caption: 'Kipróbálom',
						type: 'dismiss',
						class: 'btn btn-green'
					}
				],
				trackingId: `track_successful upload_${data._type}`,
			});
		}

		if (modalPromise) {
			modalPromise.catch(() => {
				this.goToExtra();
			});
		}

		this.captchaRef.reset();
	}

	public showApModal() {
		this.showMessageModal({
			title: 'Mi ez?',
			message: `<p><img src="/assets/img/promo/ncp-szamhaboru/img-ticket-ap.png" srcset="/assets/img/promo/ncp-szamhaboru/img-ticket-ap@2x.png 2x" class="img-fluid"></p>`,
		});
	}

	public showRegisterModal(): Promise<any> {
		const formValues = this._getActiveForm().value;
		const modal = this._modal.open(RegistrationModalComponent);
		modal.componentInstance.email = formValues.email;
		return modal.result;
	}

	public showMessageModal({title, message, trackingId, buttons, options}: MessageModalParams): Promise<any> {
		const modal = this._modal.open(MessageModalComponent, options);
		modal.componentInstance.title = title;
		modal.componentInstance.message = message;
		modal.componentInstance.trackingId = trackingId;
		modal.componentInstance.buttons = buttons;
		modal.componentInstance.options = options;
		return modal.result;
	}

	public captchaResolved(captchaResponse: string) {
		console.log('captchaResponse', captchaResponse);
		this.captchaResponse = captchaResponse;
		if (this.captchaResponse) {
			this.submitUpload();
		}
	}

	public goToExtra() {
		this._shared.email.next(this._getActiveForm().value.email);
		this.router.navigate(['/nyeremenyjatek'], { fragment: 'section-extra' });
	}
}
