import { Component, OnInit, OnDestroy } from '@angular/core';
import { UIState } from '@app/shared/services/uistate.service';
import { SectionId } from '@app/shared/models/common.model';
import { Subscription, Subject, asyncScheduler } from 'rxjs';
import { throttleTime, map, distinctUntilChanged } from 'rxjs/operators';

type SectionVisibility = {
	section: SectionId;
	status: boolean;
	inViewPercentage: {
		vertical: number;
		horizontal: number;
	};
};
type SectionVisibilities = {
	[K in SectionId]?: SectionVisibility;
};

@Component({
	selector: 'page-ncp-home',
	templateUrl: 'home.component.html',
	styleUrls: ['home.component.scss'],
})
export class NcpHomeComponent implements OnInit, OnDestroy {
	constructor(
		public uiState: UIState,
	) {}

	private _sectionVisibility: SectionVisibilities = {};
	private _sectionVisibilityChange = new Subject<SectionVisibilities>();
	private _sectionVisibilityChangeSubscription: Subscription;

	public inview(inViewport: any) {
		this._sectionVisibility[inViewport.data] = { section: inViewport.data, status: inViewport.status, inViewPercentage: inViewport.inViewPercentage };
		this._sectionVisibilityChange.next(this._sectionVisibility);
	}

	ngOnInit() {
		const throttleConfig = {
			leading: false,
			trailing: true
		};

		this.uiState.activeSection$.next('hero');

		this._sectionVisibilityChangeSubscription = this._sectionVisibilityChange.pipe(
			throttleTime(50, asyncScheduler, throttleConfig),
			map(sectionVisibility => {
				// find section with max. vertical visibility
				const activeSection = Object.values(sectionVisibility)
					.filter(section => !!section)
					.reduce((prev, current) => (prev.inViewPercentage.vertical < current.inViewPercentage.vertical) ? current : prev)
				;
				return activeSection.section;
			}),
			distinctUntilChanged(),
		).subscribe(activeSection => {
			// console.log('activeSection:', activeSection);
			this.uiState.activeSection$.next(activeSection);

			// const updatedURL = `/#section-${activeSection}`;
			// this._location.replaceState(updatedURL);
	});
	}

	ngOnDestroy() {
		this._sectionVisibilityChangeSubscription.unsubscribe();
	}

}
